.c-module-title {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 0.75em;
  font-weight: normal;
  letter-spacing: 0.22em;
  text-indent: 0.22em;
  font-size: 2rem;
}
@media screen and (max-width: 900px) {
  .c-module-title {
    font-size: 1.875rem;
  }
}
@media screen and (max-width: 768px) {
  .c-module-title {
    font-size: 1.5rem;
  }
}
.c-module-title span {
  font-family: "Roboto", -apple-system, Roboto, "Helvetica Neue", "", "Yu Gothic", YuGothic, " Medium", "Hiragino Kaku Gothic ProN", "", meiryo, sans-serif;
  font-weight: 300;
}
.c-module-title:after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  display: block;
  width: 82px;
  height: 1px;
  margin: auto;
  background: #A8936F;
}

