  opacity: 0;
}
.lazyhide[data-lazydisp=bottom] {
  transform: translateY(50px);
}
.lazyhide[data-lazydisp=top] {
  transform: translateY(-50px);
}
.lazyhide[data-lazydisp=left] {
  transform: translateX(-50px);
}
.lazyhide[data-lazydisp=right] {
  transform: translateX(50px);
}

