/*
 * Lazydisp
 *
 * [data-lazydisp]
 * 
 *
 * [usage]
 * <div data-lazydisp="bottom">something</div> 
 *
 */


 [data-lazydisp]{
  opacity: 1;
  transition: all 0.5s ease-in-out;
  transform: translate( 0, 0 ) scale( 1, 1 );
}
.lazyReady{
  transition: none;
}
.lazyhide{
  opacity: 0;
  &[data-lazydisp="bottom"]{
    transform: translateY( 50px );
  }
  &[data-lazydisp="top"]{
    transform: translateY( -50px );
  }
  &[data-lazydisp="left"]{
    transform: translateX( -50px );
  }
  &[data-lazydisp="right"]{
    transform: translateX( 50px );
  }
}